@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600&family=Space+Mono:wght@400;700&display=swap);
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: black;
  color: aliceblue;
  font-family: "Montserrat", sans-serif;
}

.main-container {
  text-align: center;
  border: 0.2rem solid white;
  margin: 5%;
}

.main-container h1 {
  font-family: "Space Mono", monospace;
  font-size: 2.5rem;
  margin: 3rem 0;
  padding: 0 1rem;
}

.main-container input {
  padding: 0.5rem;
  border: 0.2rem solid white;
  border-radius: 5px;
}

h2 {
  font-family: "Space Mono", monospace;
  font-style: oblique;
  padding: 0 1rem;
}

.astronomy article {
  line-height: 1.5;
  letter-spacing: 0.05rem;
  text-align: justify;
  padding: 3% 10% 5%;
}

.astronomy article::first-letter {
  font-size: 250%;
  font-weight: 800;
  line-height: 1;
  color: yellow;
}

iframe {
  width: 40rem;
  height: 30rem;
  border: 0.2rem solid white;
  border-radius: 5px;
  margin: 2rem 0 1rem;
}

.image-container {
  display: inline-block;
  position: relative;
  width: 40rem;
  height: 40rem;
  -webkit-clip-path: ellipse(50% 45% at 50% 50%);
          clip-path: ellipse(50% 45% at 50% 50%);
  background-color: white;
}

.image-container img {
  position: absolute;
  top: 0.2rem; /* equal to border thickness */
  left: 0.2rem;
  width: 39.6rem;
  height: 39.6rem; /* container height - (border thickness * 2) */
  -webkit-clip-path: ellipse(50% 45% at 50% 50%);
          clip-path: ellipse(50% 45% at 50% 50%);
}

.gallery {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
}

.gallery img {
  width: 30%;
  height: 30%;
  border: 0.2rem solid white;
  border-radius: 5px;
  margin: 1%;
  -webkit-align-self: center;
          align-self: center;
}

@media (max-width: 820px) {
  .main-container h1 {
    font-size: 2rem;
  }

  iframe {
    width: 30rem;
    height: 20rem;
  }

  .image-container {
    width: 30rem;
    height: 30rem;
  }

  .image-container img {
    width: 29.6rem;
    height: 29.6rem;
  }
}

@media (max-width: 650px) {
  .main-container h1 {
    font-size: 1.7rem;
  }

  .astronomy h2 {
    font-size: 1.2rem;
  }

  .astronomy article,
  p {
    font-size: 0.9rem;
  }

  iframe {
    width: 20rem;
    height: 15rem;
  }

  .image-container {
    width: 20rem;
    height: 20rem;
  }

  .image-container img {
    width: 19.6rem;
    height: 19.6rem;
  }
  .gallery img {
    width: 40%;
    height: 40%;
  }
}

@media (max-width: 414px) {
  .main-container h1 {
    font-size: 1.4rem;
    margin: 1.5rem 0;
  }

  .main-container input {
    padding: 0;
  }

  .astronomy h2 {
    font-size: 1rem;
  }

  iframe {
    width: 15rem;
    height: 10rem;
  }

  .image-container {
    width: 17rem;
    height: 17rem;
  }

  .image-container img {
    width: 16.6rem;
    height: 16.6rem;
  }

  .gallery img {
    width: 85%;
    height: 85%;
    margin: 2% auto;
  }
}

